import React, { useState, useEffect } from "react"
import { ThemeProvider } from "styled-components"

import { useSelector, useDispatch } from "react-redux"
import { setLight, setDark } from "reducers/theme"

import { light, dark } from "assets/theme"

import GlobalStyles from "components/GlobalStyles"

import Header from "./Header"
import Body from "./Body"
import Footer from "./Footer"

const Layout = ({ children }) => {
  const dispatch = useDispatch()
  const themeMode = useSelector(state => state.theme).theme

  let isSystemDarkMode = null
  if (typeof window !== "undefined") {
    isSystemDarkMode = window.matchMedia("(prefers-color-scheme: dark)").matches
  }

  let localTheme = null
  if (typeof localStorage !== "undefined") {
    localTheme = localStorage.getItem("theme")
  }

  // const [themeMode, setThemeMode] = useState(localTheme)
  const [theme, setTheme] = useState(light)

  console.log({ themeMode })

  const toggleTheme = () => {
    const nextTheme = themeMode === "dark" ? "light" : "dark"
    // setThemeMode(nextTheme)
    dispatch(nextTheme === "dark" ? setDark : setLight)
    localStorage.setItem("theme", nextTheme)
  }

  useEffect(() => {
    setTheme(themeMode === "dark" ? dark : light)
  }, [themeMode])

  console.log({ theme })

  return (
    <ThemeProvider theme={themeMode === "light" ? light : dark}>
      <GlobalStyles />
      <Header toggleTheme={toggleTheme} />
      <Body>{children}</Body>
      <Footer />
    </ThemeProvider>
  )
}

export default Layout
